.approach {
  &__top {
    padding-top: 80px;
    padding-bottom: 80px;
    background-color: #F7F8F8;

    @include small-tablet {
      padding: 40px 0;
    }
  }

  &__left {
    grid-column: 5 span;
    padding-top: 33px;

    @include big-desktop {
      margin-bottom: var(--offset);
    }

    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__right {
    grid-column: 7 / 6 span;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 8px;

    @include big-desktop {
      grid-column: 12 span;
      justify-content: flex-start;
    }


    @include small-tablet {
      grid-column: 4 span;
      flex-wrap: wrap;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-size: 22px;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__descr {
    margin: 0;
    margin-bottom: 37px;
    font-size: var(--second--size);
    line-height: 24px;
    color: var(--text-color)
  }

  &__btn {
    color: #00413E;
    border: 1px solid rgba(53, 83, 98, 0.2);
  }

  &__bottom {
    padding: 24px 0;
    background-color: var(--light-color);
  }

  &__item {
    grid-column: 2 span;
  }

  &__link {
    display: block;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    padding-top: 16px;
    padding-left: 32px;
    font-size: var(--second-size);
    line-height: 24px;
    color: var(--text-color);
    background-position: left top 15px;
    background-repeat: no-repeat;
    background-size: 24px 24px;
    transition: color .3s ease-in-out, border-color .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
    }

    &:active {
      border-color: var(--active-color);
      color: var(--active-color);
    }

    &--ted {
      background-image: url(../img/approuch-ted.png);
    }

    &--nyt {
      background-image: url(../img/approuch-nyt.png);
    }

    &--scoop {
      background-image: url(../img/approuch-scoop.png);
    }

    &--press {
      background-image: url(../img/approuch-press.png);
    }

    &--time {
      background-image: url(../img/approuch-time.png);
    }

    &--freethink {
      background-image: url(../img/approuch-freethink.png);
    }
  }
}

.approach__link.focus-visible {
  border-color: var(--accent-color);
}
