.header {
  padding: 17px 0;

  &__container {
    align-items: center;
    justify-content: space-between;
  }

  &--position {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    width: 100%;
  }

  .nav {
    align-items: center;
    padding-right: 16px;

    &__list {
      align-items: center;
      margin-right: 32px;
      gap: 51px;

      @include small-tablet {
        flex-direction: column;
        align-items: start;
        margin-right: 0;

      }
    }

    &__link {
      font-size: var(--main-size);
      color: var(--light-color);
      transition: color .3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--active-color);
      }

      @include small-tablet {
        font-size: 16px;
      }
    }

    @include small-tablet {
      position: fixed;
      left: -100%;
      top: 0;
      z-index: 100;
      width: 60%;
      height: 100vh;
      flex-direction: column;
      align-items: start;
      gap: 51px;
      padding: 40px;
      background-color: #001414;
      overflow-y: auto;
      transition: transform .5s ease-in-out;
    }
  }

  .nav__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }

  .nav.menu--active {
    transform: translateX(166%);
  }
}
