.steps {
  padding-top: 120px;
  padding-bottom: 120px;

  &__title {
    margin: 0 auto;
    margin-bottom: 57px;
    max-width: 720px;
    font-family: var(--second-family);
    font-size: 30px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 1.2px;
    color: var(--dark-color);

    @include small-tablet {
      font-size: 24px;
      line-height: 32px;
    }
  }

  &__list {
    justify-content: space-between;
    margin: 0 auto;
    max-width: 758px;
    counter-reset: steps;

    @include small-tablet {
      flex-wrap: wrap;
      gap: var(--offset);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    &::before {
      counter-increment: steps;
      content: counter(steps);
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      margin-bottom: 28px;
      font-size: 16px;
      line-height: 30px;
      color: var(--light-color);
      background: #014C48;
      border-radius: 100%;
    }

    &--line {
      position: relative;
      flex-grow: 1;

      &::before {
        display: none;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -35px;
        width: 100%;
        height: 1px;
        background: rgba(53, 83, 98, 0.2);
        transform: translateY(-50%);
      }
    }

    @include small-tablet {
      width: 100%;

      &::after {
        display: none;
      }
    }
  }

  &__descr {
    margin: 0;
    max-width: 142px;
    font-size: var(--second-size);
    line-height: 24px;
    text-align: center;
    color: rgba(53, 83, 98, 0.8);
  }

  @include small-tablet {
    padding: 60px 0;
  }
}
