.info {
  // position: relative;
  display: flex;
  height: 680px;
  background-color: #001414;

  &::before {
    content: '';
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url(../img/info-left.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include tablet {
      display: none;
    }
  }

  &::after {
    content: '';
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url(../img/info-right.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include tablet {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 82px;
    border-right: 1px solid var(--dark-color);
    border-left: 1px solid var(--dark-color);
    height: 100%;

    @include tablet {
      padding: var(--offset);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 21px;
    font-size: 22px;
    line-height: 22px;
    color: var(--light-color);
  }

  &__descr {
    margin: 0;
    font-size: var(--second-size);
    line-height: 24px;
    color: var(--light-color);
  }

  @include tablet {
height: auto;
  }

}
