.press {
  padding-top: 87px;
  padding-bottom: 87px;
  background-color: var(--light-color);

  &__top {
    margin-bottom: 33px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include small-tablet {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-weight: 400;
    font-size: 27px;
    font-family: var(--second-family);
    line-height: 38px;
    letter-spacing: 1.2px;
  }

  &__link {
    color: #00413E;
    border: 1px solid rgba(53, 83, 98, 0.20);
  }

  &__item {
    grid-column: 4 span;
  }

  .press-card {
    position: relative;
    padding-top: 16px;
    min-height: 145px;
    border-top: 1px solid rgba(53, 83, 98, 0.30);

    &__top {
      margin-bottom: 11px;
      display: flex;
      align-items: center;
      gap: 16px;
    }

    &__author {
      margin: -18px 0;
      padding: 18px 0;
      color: rgba(53, 83, 98, 0.60);
      font-size: 11px;
      line-height: 16px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      padding-left: 32px;
      background-position: left top 15px;
      background-repeat: no-repeat;
      background-size: 24px 24px;
      transition: color .3s ease-in-out, border-color .3s ease-in-out;

      &--ted {
        background-image: url("../img/press-icon-1.png");
      }

      &--correctional {
        background-image: url("../img/press-icon-2.png");
      }

      &--departament {
        background-image: url("../img/press-icon-3.png");
      }

      &--newyork {
        background-image: url("../img/press-icon-4.png");
      }

      &--correctionsi {
        background-image: url("../img/press-icon-5.png");
      }

      &--rightoncrime {
        background-image: url("../img/press-icon-6.png");
      }
    }

    &__date {
      color: rgba(53, 83, 98, 0.60);
      font-size: 11px;
      line-height: 16px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
    }

    &__title {
      margin: 0;
      color: #00413E;
      font-size: 17px;
      line-height: 24.65px;
      transition: color .3s ease-in-out;
    }

    &__link {
      color: inherit;
      transition: color .3s ease-in-out;

      &::after {
        position: absolute;
        content: "";
        inset: 0;
      }

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--active-color);
      }
    }
  }

  @include small-tablet {
    padding: 60px 0;
  }
}

.press-card__link.focus-visible {
  &::after {
    outline: 1px solid var(--active-color);
  }
}
