.footer {
  &__top {
    padding-top: 112px;
    padding-bottom: 137px;
    background-color: #001414;

    @include small-tablet {
      padding: 60px 0;
    }
  }

  &__column {
    grid-column: 2 span;

    &:last-child {
      grid-column: 11 / 2 span;

      @include small-tablet {
        grid-column: 2 span;
        }
    }
  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 23px;
    padding-bottom: 24px;
    color: #7D9798;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background-color: #25B895;
    }
  }

  &__link {
    color: #EDECEB;
    font-size: var(--second-size);
    line-height: 30px;
    transition: color .3s ease-in-out;


    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__bottom {
    padding-top: 40px;
    padding-bottom: 63px;
    background-color: #000F0F;

    &-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-text {
      display: flex;
      align-items: center;
    }

    @include small-tablet {
      padding-bottom: 40px;
    }
  }

  &__sublist {
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 8px;

    &-link {
      color: #E5ECEC;
      font-size: 10px;
      line-height: 10px;
      transition: color .3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--active-color);
      }
    }
  }

  &__copy {
    color: #E5ECEC;
    font-size: 9px;
    line-height: 10px;
  }

  &__rights {
    color: #E5ECEC;
    font-size: 10px;
    line-height: 10px;
  }
}

.footer__link.focus-visible {
  outline: 1px solid var(--active-color);
}

.footer__sublist-link.focus-visible {
  outline: 1px solid var(--active-color);
}
